import React from "react";
import { graphql, Link } from "gatsby";
import Layout from "../components/layout";
import ShareTab from "../components/shared/share-tab";
import BottomCta from "../components/shared/bottom-cta";
import SectionTitle from "../components/shared/section-titles";
import { PromoCard } from "../pages/promocii";
import { Breadcrumbs } from "../components/shared/breadcrumbs";
import SEO from "../components/seo";
import { BgWrapper } from "../components/shared/components";

const breadcrumbs = [
  {
    text: 'Начало',
    link: '/',
  },
  {
    text: 'Промоции',
    link: '/promocii/',
  },
]

export default ({ data }) => {
  const post = data.post;
  const randomPosts = data.randomPosts;
  let b = breadcrumbs;
  if (b[b.length - 1].text !== post.frontmatter.title)
    b.push({ text: post.frontmatter.title });

  return (
    <BgWrapper bg={data.bg.childImageSharp.fluid.src}>
      <Layout>
        <SEO
          title={post.frontmatter.title}
        />
        <div className='container' style={{ paddingTop: '12rem' }}>

          <Link to={'/promocii/'} className='link'><span className='icon back-blue-arrow mr-1'></span> Назад</Link>
          <h1 className='my-3'>{post.frontmatter.title}</h1>
          <Breadcrumbs items={b} />

          <div className='my-4 d-flex justify-content-start'>
            <div className='col-12 col-lg-8 p-0 m-0'>
              <img src={post.frontmatter.image.childImageSharp.fluid.src} className='img-fluid' alt={post.frontmatter.title} />
              <div className='mt-5' dangerouslySetInnerHTML={{ __html: post.html }} />
            </div>
          </div>

          <ShareTab path={post.fields.slug} />

          <BottomCta
            bgImg={data.ctaBg.childImageSharp.fluid.src}
            removeSpacing={true}
          >
            <div className='d-flex justify-content-center align-items-center' style={{ minHeight: '18rem' }}>
              <div>
                <h2 className='mb-4 d-none d-md-block text-white'>При всякакви въпроси и<br /> запитвания</h2>
                <h2 className='mb-5 d-block d-md-none text-white'>При всякакви въпроси и запитвания</h2>
                <a href='tel:+359882991611' className='btn btn-primary green'>Обади се на 088 299 1611</a>
              </div>
            </div>
          </BottomCta>

          <SectionTitle>
            <h2>Други промоции</h2>
            <hr className='center' />
          </SectionTitle>

          <div className='row mb-5'>
            {randomPosts.edges.map(({ node }, index) => (
              <div className='col-6 col-lg-3'>
                <PromoCard {...node.frontmatter} key={index} slug={node.fields.slug} isBig={false} />
              </div>
            ))}
          </div>
        </div>
      </Layout>
    </BgWrapper>
  )
}

export const query = graphql`
  query ($slug: String!) {
    post: markdownRemark(fields: {slug: {eq: $slug}}) {
      html
      fields {
        slug
      }
      frontmatter {
        title
        image {
          childImageSharp {
            fluid {
              src
            }
          }
        }
      }
    }
    ctaBg: file(relativePath: {eq: "promo/promo-cta.png"}) {
      childImageSharp {
        fluid (maxHeight: 700) {
          src
        }
      }
    }
    bg: file(relativePath: {eq: "promo/promo-bg.png"}) {
      childImageSharp {
        fluid (maxWidth: 2000) {
          src
        }
      }
    }
    randomPosts: allMarkdownRemark(limit: 4) {
      edges {
        node {
          id
          fields {
            slug
          }
          frontmatter {
            title
            subtitle
            image {
              childImageSharp {
                fluid {
                  src
                }
              }
            }
          }
        }
      }
    }
  }
`;